@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumBarunGothic@1.0/nanumbarungothicsubset.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "NanumBarunGothic", sans-serif;
  background-color: #f2f2f2;
  line-height: 1.6em;
}

.container {
  padding-top: 55px;
  text-align: center;
  background: #f2f2f2;
  height: 100vh;
  box-sizing: border-box;
}

input {
  outline: none;
}

div.box {
  margin: 8px;
  padding: 1em;
  border-radius: 5px;
  background-color: #fff;
  margin-bottom: 2em;
  box-shadow: 0px 3px 0px -1px rgba(0, 0, 0, 0.08);
}

p {
  margin: 0;
}

img {
  max-width: 100%;
}
@font-face {
  font-family: Norwester;
  src: url("/font/norwester.otf") format("opentype");
}

header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  padding: 14px 0;
  text-align: center;
  font-size: 1.3em;
  background: #fff;
  font-family: Norwester, sans-serif;
  border-bottom: 1px #eaeaea solid;
  z-index: 999;
}

a {
  color: inherit;
  text-decoration: none;
}
footer {
  position: fixed;
  bottom: 0;
}
footer {
  position: fixed;
  bottom: 0;
  background-color: #fff;
  padding: 7px 0 0 0;
}

.container-home .box {
  margin: 0 10px;
}

.container-home .box .meal-label {
  font-size: 12px;
  line-height: 1.4em;
}

.container-home .box .title {
  font-weight: bold;
  text-align: left;
  border-bottom: 1px #f2f2f2 solid;
  padding: 0 0 10px 0;
}

.container-home .box .title img {
  float: right;
  margin-top: 5px;
}

.meal-thumb-wrap {
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
}

.meal-thumb {
  width: 30%;
}

.meal-thumb .inner {
  width: 100%;
  background-color: #fafafa;
  border-radius: 2px;
  padding: 1.8em 0;
  min-height: 96px;
  margin-bottom: 0.5em;
  box-sizing: border-box;
}

.meal-thumb .breakfast {
  background-size: cover;
  background-image: url("/images/meal_thumb.png");
}

.meal-thumb .lunch {
  background-size: cover;
  background-image: url("/images/meal_thumb_2.jpeg");
}

.meal-thumb img {
  width: 30px;
  opacity: 0.2;
}

.square-crop {
  width: 375px;
  position: relative;
  overflow: hidden;
  height: 100vh;
  display: block;
}

.photo-frame {
}

.select-photo {
  width: 100%;
  background-color: #666;
}

input.food {
  box-shadow: 0 1px 0 0 #b5b5b5;
  border: 0;
  width: 7em;
  margin: 2em 0 0.4em 0;
  text-align: center;
  font-size: 2em;
  padding: 0 0 0.2em 0;
  background: transparent;
}

p.helper-text {
  font-size: 12px;
  color: #666;
}

.camera-button {
  width: 41px;
  height: 41px;
  background-color: #cacaca;
  padding: 10px;
  border-radius: 100%;
  margin: 90px auto;
}

.camera-button .inner {
  background-color: #fafafa;
  border-radius: 100%;
  width: 1px;
  height: 1px;
  padding: 20px;
}

.sweet-loading {
  position: fixed;
  top: 0;
  width: 100%;
  padding-top: 83%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}

.title {
  text-align: center;
  padding: 1em;
  font-weight: bold;
}
.blink {
  background-color: #fff;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
}

.hide {
  display: none;
}

.timer-graphic {
  margin: 2em 2em 3em 2em;
}
.sticky {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  padding: 1em 0;
  color: #fff;
  font-weight: bold;
  background-color: #f86767;
}

.report-header {
  background-image: -webkit-gradient(linear, left top, right bottom, from(#32cca4), to(#2dbd97));
  background-image: linear-gradient(to right bottom, #32cca4, #2dbd97);
  padding: 3em 0 6em 0;
  color: #fff;
}

.report-header p {
  font-size: 1.3em;
  line-height: 1.6em;
}

.report-box-wrap {
  margin-top: -80px;
}

.report-box {
  margin: 1em;
  background-color: #fff;
  padding: 1em;
  border-radius: 10px;
  position: relative;
}

.report-box .desc {
  font-size: 0.8em;
}

.report-box .divider {
  width: 100%;
  height: 1px;
  background-color: #e2e2e2;
  margin: 0.4em 0;
}

.report-box .red {
  color: #f86052;
}

.report-box .green {
  color: #32cca4;
}

.report-box h4 {
  margin: 0;
}

.report-box .thumb-wrap {
  margin-top: 1em;
}

.report-box .thumb-item {
  width: 30%;
  margin: 0 4px;
  display: inline-block;
  position: relative;
}

.report-box .thumb-item .caption {
  position: absolute;
  bottom: 6px;
  font-size: 10px;
  text-align: center;
  width: 100%;
  background: rgba(255, 255, 255, 0.5);
  line-height: 1.9em;
}

.container-report .sticky {
  margin-top: 3em;
  position: relative;
}

.emoji-wrap {
  display: flex;
  justify-content: space-around;
  margin-top: 1em;
}

.emoji-wrap img {
  width: 50%;
}

.selected {
  font-weight: bold;
  color: #f86767;
}

.icon-wrap {
  margin-top: 15px;
}

.icon-wrap img {
  width: 20px; margin: 0 2px;
}

/* Reset Select */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #f86767;
  background-image: none;
  color: #fff;
  cursor: pointer;
  border-radius: 21px;
  font-size: 1.2em;
  background-image: url("http://localhost:3000/images/arrow.png");
  background-repeat: no-repeat;
  background-position: right 20px center;
}
/* Remove IE arrow */
select::-ms-expand {
  display: none;
}
/* Custom Select */
.select {
  position: relative;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #2c3e50;
  overflow: hidden;
}
select {
  padding: 0.6em 3em 0.6em 1.5em;
  color: #fff;
  cursor: pointer;
  display: block;
  margin: 1em auto;
  border-radius: 2em;
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #34495e;
  cursor: pointer;
  pointer-events: none;
  -webkit-transition: 0.25s all ease;
  transition: 0.25s all ease;
}
/* Transition */
.select:hover::after {
  color: #f39c12;
}

p.desc {
  font-size: 14px;
  color: #666;
}

.container-setting img {
  width: 32px;
}

